.individualProduct {
  display: flex;
  flex-direction: column;
  color: rgb(219, 215, 215);
  width: 250px;
  background-color: rgb(17, 17, 17);
  box-shadow: 2px 2px 25px 2px rgba(0, 0, 0, 0.4);
  padding: 12px;
  padding-bottom: 20px;
  margin-top: 15px;
  margin-left: 15px;
  height: 300px;
  border-radius: 5px;
  transition: transform 0.1s;
  position: relative;
}
.individualProduct:hover {
  cursor: pointer;
  transform: scale(1.03, 1.03) !important;
}
.individualProduct img {
  width: 99%;
  height: 75%;
}
.individualProduct h4 {
  margin-top: 10px;
  font-size: 20px;
  color: goldenrod;
}
.individualProduct .desc {
  font-weight: 300;
  font-size: 15px;
  position: absolute;
  bottom: 20px;
  width: 100%;
}
.individualProduct span:nth-child(2) {
  color: goldenrod;
}
.searchVal {
  color: rgb(175, 175, 175);
  margin-top: 10px;
  /* background-color: black; */
  box-shadow: 1px 1px 15px 3px rgba(0, 0, 0, 0.5);
  padding: 8px 25px;
  border-radius: 5px;
  font-size: 25px;
}
.containerRight {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  align-items: center;
  margin-bottom: 20px;
}
.products {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.pagination {
  display: flex;
  list-style-type: none;
  margin-top: 20px;
}
.pagination .links {
  text-decoration: none;
  color: white;
  font-weight: 600;
}
.pagination .container {
  margin-left: 10px;
  background: rgb(31, 30, 30);
  box-shadow: 2px 2px 15px 2px rgba(19, 18, 18, 0.8);
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  transition: all 0.2s;
}
.pagination .container:hover {
  cursor: pointer;
  background: rgb(48, 47, 47);
  transform: scale(1.03, 1.03);
}
.pagination .activeClass {
  background: goldenrod;
}
.pagination .container:active {
  background: goldenrod;
}
@media screen and (max-width: 1320px) {
  .containerRight {
    margin-left: -10px;
  }
}
.errorSection {
  color: rgb(196, 186, 186);
  height: 65vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.errorIcon {
  color: goldenrod;
  font-size: 60px !important;
}
.errorContainer {
  background-color: black;
  height: 80%;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  box-shadow: 2px 2px 15px 2px rgba(0, 0, 0, 0.5);
}
